import React from 'react';
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Image from 'react-bootstrap/Image'


// Styling import
import './Contact.scss'

// Image import
import facebook from '../../pictures/facebook.webp';
import instagram from '../../pictures/instagram.png';
import linkedin from '../../pictures/linkedin.png';
import github from '../../pictures/github.svg';
import stackoverflow from '../../pictures/stackoverflow.png'




function Contact(props) {
    return (
        <div className='contact-body container' id='contact-section'>
            <div className="contact-imgBox">
                <a href='https://www.facebook.com/junyi.hoo' target='_blank' ><Image src={facebook} className='contact-pic' /></a>
                <a href='https://www.instagram.com/junyi_hoo/' target='_blank'><Image src={instagram} className='contact-pic' /></a>
                <a href='https://www.linkedin.com/in/jun-yi-hoo-268aa5184/' target='_blank'><Image src={linkedin} className='contact-pic' /></a>
                <a href='https://github.com/junyihoo1997' target='_blank'><Image src={github} className='contact-pic' /></a>
                <a href='https://stackoverflow.com/users/7617903/hoo-jun-yi' target='_blank'><Image src={stackoverflow} className='contact-pic' /></a>
            </div>
        </div>
    );
}

export default Contact;